/** @jsx jsx */
import { jsx } from '@emotion/core';
import MUIDataTable, { MUIDataTableColumn } from "mui-datatables";
import options from 'components/table/options';
import { FaShieldAlt } from "react-icons/fa";
import { Invited, InviteStatusIcon, Joined } from "components/AccountStatus";
import { MdSend } from "react-icons/md";
import { GoCheck } from "react-icons/go";
import styled from '@emotion/styled';

export const Role = styled.span`
  display: block;
`

const data = [
  [true, "Walter White", "Breaking Bad Pty Ltd", "walter.white@bb.com", "+61 456 789 012", ["Director", "Shareholder"], 3, 100],
  [false, "Saul Goodman", "WM Limited", "saul.goodman@itsallgoodman.com", "+61 456 789 012", ["Director"], 0, 150],
  [false, "Kim Wexler", "WM Limited", "kim.wexler@wm.com", "+61 456 789 012", ["Shareholder"], 1, 200],
  [false, "Tuco", "Salamanca Pty Ltd", "tuco@salamanca.com", "+61 555 555 555", [], 0, 250],
];

function ContactsList() {
  const columns: MUIDataTableColumn[] = [
    {
      name: "", // Applicant Admin
      options: {
        customBodyRender: (value: any, tableMeta, updateValue) => {
          return (
            Boolean(value) ? <FaShieldAlt /> : null
          )
        }
      }
    },
    {
      name: "Name"
    },
    {
      name: "Business"
    },
    {
      name: "Email"
    },
    {
      name: "Phone",
    },
    {
      name: "Role",
      options: {
        customBodyRender: (value: unknown, tableMeta, updateValue) => {
          return (
            (value as string[]).map((v, i) =>
              <Role key={i}>{v}</Role>
            )
          )
        }
      }
    },
    {
      name: "", // Account Status
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            Number(value) === 1 ? <InviteStatusIcon css={Invited}><MdSend /></InviteStatusIcon> :
            Number(value) === 3 ? <InviteStatusIcon css={Joined}><GoCheck /></InviteStatusIcon> : null
          )
        }
      }
    }
  ];

  return (
    <MUIDataTable
      title=""
      data={data}
      columns={columns}
      options={options}
    />
  )
}

export default ContactsList;
